.Body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: linear-gradient(to right, #282c34 , #1d2025 );
  }
  
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  /***********************************************/
  .App {
    text-align: center;
    background-color: #F71735;
    width: auto;
    height: 500px;
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.29);
    position: relative;
  
  
  }
  .titulo{
   color: #282c34;
   text-shadow: 2px 2px 2px #1d202538; 
  }
  .Loginpai{
    display: flex;
    flex-direction: column;
    width: 500px;
    align-items: center;
    justify-content: center;
    height: 50vh;  
  }
  
  .Loginpai > input {
    width: 300px;
    margin: 10px;
    border-style: none;
    border-radius: 5px;
  }
  .emailesenha{
    height: 50px;
    box-shadow: 5px 5px 5px #282c3431;
    background-color: #F71735;
    outline:none ;
  }
  .emailesenha:focus{
    background-color: #282c34;
    transition: 1s;
  }
  ::placeholder{
    color: white;
  }
  .botao{
    height: 50px;
    font-size: 130%;
    box-shadow: 5px 5px 5px #282c342d;
  }
  .botao:hover{
    scale: 1.05;
    cursor: pointer;
    transition: 1s;
    background-color: #282c34;
    color: white;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.285);
  }
  .botao:not(hover){
    transition: 1s;
    
  }
  .rodape{
    background-color:  #F71735;
    position: absolute;
    bottom: 0;
    width: 100%;
    margin: auto;
    padding-bottom: 20px;
  }
  .botaoc{
    border-style: none;
    border-radius: 5px;
    height: 25px;
    font-size: medium;
    background-color: #F71735;
  
  }
  .botaoc:hover{
    scale: 1.05;
    cursor: pointer;
    transition: 1s;
    background-color: #282c34;
    color: white;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.285);
  }
  .botaoc:not(hover){
    transition: 1s;
    
  }
  